<template>
  <div>
    <b-pagination
      v-model="currentPage"
      :total-rows="books.length"
      :per-page="perPage"
      aria-controls="image-table"
    ></b-pagination>
    <b-table
      id="image-table"
      ref="table"
      hover
      fixed
      :items="bookItems"
      :fields="fields"
      small
      :per-page="perPage"
      :current-page="currentPage"
      @row-clicked="rowClicked"
    >
      <template #cell(isTaken)="data">
        <b-icon v-if="data.item.userId > 0" icon="check-square" variant="success" scale="2"></b-icon>
        <b-icon v-else icon="x-circle" variant="danger" scale="2"></b-icon>
      </template>
    </b-table>
    <b-pagination
      v-model="currentPage"
      :total-rows="books.length"
      :per-page="perPage"
      aria-controls="image-table"
    ></b-pagination>
  </div>
</template>

<script>

  import { mapActions, mapState } from 'vuex';

  export default {
    data() {
      return {
        fields: ['name', 'description', 'author', { key: 'isTaken', tdClass: 'align-middle' }],
        bookItems: [],
        currentPage: 1,
        perPage: 5
      }
    },

    computed: {
      ...mapState([
        'books'
      ])
    },

    watch: {
      currentPage(nVal, oVal) {
        this.books.slice(0, (this.currentPage ) * this.perPage).map(el => {
            if(!this.bookItems.includes(el))
              this.bookItems.push(el);
        });
        console.log("size je " + this.bookItems.length);
      },

      books(nVal, oVal) {
        this.currentPage = 1;
        this.bookItems = [];
        nVal.slice((this.currentPage-1) * this.perPage, (this.currentPage ) * this.perPage).map(el =>{
          this.bookItems.push(el);
        });
      }
    },

    mounted() {
      this.bookItems = [];
        this.bookItems = this.books.slice((this.currentPage-1) * this.perPage, (this.currentPage ) * this.perPage).map(el =>{
          this.bookItems.push(el);
        });
    },

    methods: {
      ...mapActions([
        'getBook'
      ]),

      rowClicked(record, index) {
        this.$router.push({ name: 'SingleBook', params: { id: record.id } });
      }
    }
  }

</script>

<style scoped>
  .pagination {
    justify-content: center;
  }
</style>