<template>
  <div>
    <Header :subtitle="subtitle"/>
    <h1><b>Book:</b> {{bookName}}</h1>
    <h2><b>Author:</b> {{author}}</h2>
    <h2><b>Description:</b> {{description}}</h2>
    <h2><b>Unique owner:</b> {{uniqueOwner}}</h2>
    <b-form @submit="removeBookLocal">
      <b-button type="submit" :disabled="!(currentUser && currentUser.name == uniqueOwner)" variant="outline-danger">Throw away my book!</b-button>
    </b-form>
    <br>
    <b-form @submit="buyBookLocal">
      <b-button :disabled="!(this.bookOwnerId == 0)" variant="outline-success" type="submit">Get this book!</b-button>
    </b-form>
  </div>
</template>

<script>

  import Header from '@/components/Header.vue';
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'Single',

    components: {
      Header
    },

    data() {
      return {
        image: null,
        subtitle: '',
        bookName: '',
        author: '',
        description: '',
        uniqueOwner: '',
        currentUser: [],
        bookOwnerId: 0
      }
    },
    computed: {
      ...mapState([
        'books'
      ])
    },
    methods: {
      ...mapActions([
        'getItem',
        'getBook',
        'getBookOwner',
        'getCurrentUser',
        'buyBook',
        'removeBook'
      ]),
      buyBookLocal(e) {
        e.preventDefault();
        this.buyBook({bookId: this.$route.params.id, userId: this.currentUser.id}).then(res=>{
        this.bookOwnerId = this.currentUser.id;
        });
      },
      removeBookLocal(e){
        e.preventDefault();
        this.removeBook(this.$route.params.id).then(res =>{
          this.bookOwnerId = 0;
        });
      }
    },
    watch:{
      books(nVal, oVal){
          this.getBook(this.$route.params.id).then(res => {
          console.log(res.userId + " uid sada za 7" );
          this.bookName = res.name;
          this.author = res.author;
          this.description = res.description;
          if(res.userId == 0){
            this.uniqueOwner = "Nobody";
            this.bookOwnerId = 0;
          }
          else{
            this.getBookOwner(res.userId).then(user => {
              if(user){
                this.uniqueOwner = user.name;
                this.bookOwnerId = res.userId;
              }
              else{
                this.uniqueOwner = "User not found / deleted";
                this.bookOwnerId = 0;
              } 
            });
          }
      });
      }
    },

    mounted() {
      this.getBook(this.$route.params.id).then(res => {
          console.log(res);
          this.bookName = res.name;
          this.author = res.author;
          this.description = res.description;
          if(res.userId == 0){
            this.uniqueOwner = "Nobody";
            this.bookOwnerId = 0;
          }
          else{
            this.getBookOwner(res.userId).then(user => {
              if(user){
                this.uniqueOwner = user.name;
                this.bookOwnerId = res.userId;
              }
              else{
                this.uniqueOwner = "User not found / deleted";
                this.bookOwnerId = 0;
              } 
            });
          }
      });

      this.getCurrentUser().then(user => {
        this.currentUser = user;
      });
    }
  }

</script>