<template>
  <div id="app">
    <Header :subtitle="subtitle"/>
    <BookList />
  </div>
</template>

<script>
  import Header from '@/components/Header.vue';
  import BookList from '@/components/BookList.vue';
  import { mapActions } from 'vuex';

  export default {
    name: 'Department',
    
    components: {
      Header,
      BookList
    },

    data() {
        return {
            subtitle: '',
        }
    },

    watch: {
      $route() {
        this.subtitle = this.$route.params.name;

        this.fetchBooks();
      }
    },

    mounted() {
        this.subtitle = 'All Books';
        this.fetchBooks();
    },

    methods: {
      ...mapActions([
        'fetchBooks'
      ])
    }
  }
</script>

