        }
    },

    watch: {
      $route() {
        this.subtitle = this.$route.params.name;
      
      }
    },

    mounted() {
        this.subtitle = this.$route.params.name;
    },

    methods: {
      ...mapActions([
        
      ])
    }
  }
</script>

