<template>
  <div>
        <h1><b>Event:</b> {{$route.params.name}}</h1>
        <h2><b>Date:</b> {{eventDetails.date}} </h2>
        <h2><b>Time:</b> {{eventDetails.time}} </h2>
        <h2><b>Host:</b> {{eventDetails.name}} </h2>
        <h3><b>Desciption:</b> {{eventDetails.description}} </h3>
        <h3><b>Guests</b>: {{eventDetails.guests}} </h3>
        <b-form @submit="bookEvent">
          <b-button v-if="dismissCountDown == 0" variant="outline-success" type="submit">I'm coming!</b-button>
        </b-form>
        <b-alert
          :show="dismissCountDown"
          dismissible
          variant="success"
          @dismissed="dismissCountDown=0"
          @dismiss-count-down="countDownChanged"
        >
          One seat booked!
        </b-alert>
  </div>
</template>

<script>

  import { mapActions, mapState } from 'vuex';

  export default {

    data() {
      return {
          eventDetails: [],
          dismissSecs: 5,
          dismissCountDown: 0,
      }
    },

    computed: {
      ...mapState([
        'events'
      ])
    },

    watch: {
        events(nVal, oVal){
          this.getEvent(this.$route.params.id).then(obj => {
            this.eventDetails = obj;
          });
        },

        $route(){
          this.dismissCountDown = 0;
          this.getEvent(this.$route.params.id).then(obj => {
            this.eventDetails = obj;
          });
        }
    },
    mounted() {
      this.getEvent(this.$route.params.id).then(obj => {
            this.eventDetails = obj;
          });
    },

    methods: {
      ...mapActions([
        'getEvent',
        'eventComing'
      ]),
      bookEvent(e){
          e.preventDefault();
          this.eventComing(this.$route.params.id).then(res =>{
            console.log("res " + res);
              this.showAlert();
          })
      },
      countDownChanged(dismissCountDown) {
        if(dismissCountDown == 0){

        }
        this.dismissCountDown = dismissCountDown
      },
      showAlert() {
        this.dismissCountDown = this.dismissSecs
      },
    }
  }

</script>