
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: '',
    events: [],
    books: [],
    messages: []
  },

  mutations: {
    addBook(state, book){
      state.books.push(book);
    },
    addMessage(state, mssg){
      state.messages.push(mssg);
    },
    addBooks(state, books){
      state.books = books;
    },
    addMessages(state, mssgs){
      state.messages = mssgs;
    },
    addEvents(state, evns){
      state.events = evns;
    },
    addEvent(state, event){
      state.events.push(event);
    },

    setToken(state, token) {
      state.token = token;
      localStorage.token = token;
    },

    removeToken(state) {
      state.token = '';
      localStorage.token = '';
    },
  },

  actions: {
    fetchEvents({commit}){
        fetch('/api/events')
          .then(obj => obj.json())
            .then(res => commit('addEvents', res));
    },
    fetchMessages({commit}){
      fetch(`/api/messages/user/all`, {
            headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}`}
          }).then(obj => obj.json())
            .then(messages =>
              {
                commit('addMessages', messages)
              });  
    },
    fetchBooks({commit}){
        fetch('/api/books')
          .then(obj => obj.json())
            .then(res => commit('addBooks', res));
    },
    updateUser({commit}, form){
      return new Promise((resolve, reject) => {
        fetch(`/api/users/`, {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}`},
          body: JSON.stringify(form)
        }).then(obj => obj.json())
        .then(res =>{
          resolve(res);
        });  
      });
    },
    getCurrentUser({commit}){
      return new Promise((resolve, reject) => {
        fetch(`/api/users/current/data`, {
          headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}`}
        }).then(obj => obj.json())
          .then(res =>{
            resolve(res);
          });  
      });
    },
    getUserIdByName({commit}, name){
      return new Promise((resolve, reject)=>{
        fetch(`/api/users/name/${name}`)
        .then( res => res.json() )
        .then( userObj => {
              resolve(userObj.id);
          });
      });
    },
    getBook({commit, state}, id){
      return new Promise( (resolve, reject) => {
        const book = state.books.filter(book => book.id == id)[0];
        if(book){
          resolve(book);
        }else{
          fetch(`/api/books/${id}`)
            .then(obj => obj.json())
              .then(res => {
                commit('addBook', res);
                resolve(res);
              });
        }
      });
    },
    getBookOwner({commit}, id){
      return new Promise((resolve, reject) => {
        fetch(`/api/users/${id}`)
        .then(obj => obj.json())
          .then(res => {
            resolve(res);
          });
      });
    },
    getEvent({commit, state}, id){
      return new Promise( (resolve, reject) => {
        const event = state.events.filter(e => e.id == id)[0];
        if(event){
          resolve(event);
        }else{
          fetch(`/api/events/${id}`)
            .then(obj => obj.json())
              .then(res => {
                commit('addEvent', res);
                resolve(res);
              });
        }
      });
    },
    sendEmail({commit}, {name, body}){
      return new Promise((resolve, reject) => {
        fetch(`/api/users/current/data`, {
          headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}`}
        }).then(obj => obj.json())
          .then(user =>{
              const data = {
                userId: user.id,
                body: body
              };
              fetch('/api/messages/', {
                    method: 'POST',
                    headers: { 
                    'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}` 
                    },
                    
                    body: JSON.stringify(data)
                })
                .then( obj => obj.json() )
                .then( res => {
                      resolve(res);
                });
  
        });
      });   
    },
    buyBook({commit}, {bookId, userId}){
      console.log("book id " + bookId +  " user id " + userId);
      const input = {
        userId: userId
    };
    return new Promise((resolve, reject) => {
      fetch('/api/books/take/' + bookId,
        {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}` },
            body: JSON.stringify(input)
        })
        .then( res => res.json() )
        .then( data => {
            console.log("Book bought");
            fetch('/api/books')
              .then(bobj => bobj.json())
              .then(bres => {
                  console.log("updatuj bree " + bres);
                  commit('addBooks', bres)
              });
            resolve(data);
        });
      });
    
    },
    eventComing({commit}, id){
      return new Promise((resolve, reject) => {
        fetch('/api/events/coming/' + id,
        {
          headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}` }
        })
        .then( res => res.json() )
        .then( data => {
          fetch('/api/events')
          .then(obj2 => obj2.json())
            .then(res2 => {
              commit('addEvents', res2)
              resolve(res2);
            });
        });
      });
    },
    removeBook({commit}, bookId){
      const input = {
        userId: 0
      };
      return new Promise((resolve, reject) => {
        fetch('/api/books/take/' + bookId,
          {
              method: 'PUT',
              headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}` },
              body: JSON.stringify(input)
          })
          .then( res => res.json() )
          .then( data => {
              fetch('/api/books')
                .then(bobj => bobj.json())
                .then(bres => {
                    commit('addBooks', bres)
                });
              resolve(data);
          });
        });
    },
    getEmails({commit}){
        return new Promise((resolve, reject) => {
          fetch(`/api/messages/user/all`, {
            headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}`}
          }).then(obj => obj.json())
            .then(emails =>{
              console.log( " duzina");
              resolve(emails);
            });              
        });
    },
    register({ commit }, obj) {
      fetch('/api_register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
      }).then( res => res.json() )
        .then( tkn => commit('setToken', tkn.token) );
    },

    login({ commit }, obj) {
      fetch('/api_login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
    }).then( res => res.json() )
      .then( tkn => {
        if (tkn.msg) {
          alert(tkn.msg);
        } else {
          commit('setToken', tkn.token)
        }
      });
    },

    socket_message({ commit }, msg) {
      fetch(`/api/messages/user/all`, {
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${localStorage.token}`}
      }).then(obj => obj.json())
        .then(messages =>
          {
            commit('addMessages', messages)
          });  
        }
  }
})
