<template>
  <div id="profile">
    <Header subtitle="My profile"/>

     <b-alert
      :show="dismissCountDown"
      dismissible
      variant="success"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
      User updated!
    </b-alert>
    <b-form @submit="onSubmit">
      <b-form-group label="User Name:" label-for="name">
        <b-form-input id="name" v-model="form.name" placeholder="Enter name" required></b-form-input>
      </b-form-group>

      <b-form-group label="Password:" label-for="password">
        <b-form-input id="password" v-model="form.password" type="password"></b-form-input>
      </b-form-group>

      <b-form-group label="Email:" label-for="name">
        <b-form-input id="email" v-model="form.email"  required></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Update</b-button>
    </b-form>
    <h1>Send message</h1>
    
    <b-form @submit="sendEmailLocal">
        <b-form-input v-model="toEmail" placeholder="To:"></b-form-input>
        <b-form-textarea
        id="textarea"
        v-model="bodyEmail"
        placeholder="Message body..."
        rows="3"
        max-rows="6"
        ></b-form-textarea>
        <b-button type="submit" variant="primary">Send</b-button>
    </b-form>
    <b-alert
      :show="dismissCountDown2"
      dismissible
      variant="success"
      @dismissed="dismissCountDown2=0"
      @dismiss-count-down="countDownChanged2"
    >
      Message sent!
    </b-alert>
    <h1>My inbox</h1>
    <b-table
      hover
      fixed
      :items="items"
      :fields="fields"
      small
    ></b-table>
  </div>
</template>

<script>
  import Header from '@/components/Header.vue';
  import { mapActions, mapState } from 'vuex';

    window.addEventListener('beforeunload', function(e) {
        e.preventDefault();
        e.returnValue = '';
        console.log("jess");
    });

  export default {
    name: 'Profile',
    
    components: {
      Header
    },
     
    data() {
      return {
        fields: ['time', 'body'],
        items: [],
        toEmail: '',
        bodyEmail: '',
        form: {
          name: '',
          password: '',
          email: '',
          mod: false,
          admin: false
        },
        dismissSecs: 5,
        dismissCountDown: 0,
        dismissSecs2: 5,
        dismissCountDown2: 0
      }
    },

    computed: {
      ...mapState([
        'messages',
        'token'
      ])
    },

    watch: {
        messages(nVal, oVal){
            this.items = [];
            console.log('New val je ' + nVal.length + " " + this.items.length);
            nVal.forEach(e =>{
                let date = e.createdAt.substring(0,10) + " " + e.createdAt.substring(11, 16) + " H";
                let tmp = {time: date, body: e.body};
                this.items.push(tmp);
            });
        }
    },
    mounted() {
        this.getCurrentUser().then(usr =>{
            this.form = usr;
            this.form.password = '';
        });
        this.fetchMessages();
    },

    methods: {
      ...mapActions([
        'updateUser',
        'getCurrentUser',
        'sendEmail',
        'getEmails',
        'getBookOwner',
        'fetchMessages',
        'getUserIdByName'
      ]),

      onSubmit(e) {
        e.preventDefault();

        this.updateUser(this.form).then(res =>{
            if(res)
                this.showAlert();
        });
      },
      sendEmailLocal(e){
          e.preventDefault();
          this.getUserIdByName(this.toEmail).then(userId => {
              this.$socket.emit('message', {userId:userId, body: this.bodyEmail, token: this.token});
          });
      },
      countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert() {
        this.dismissCountDown = this.dismissSecs
      },
      countDownChanged2(dismissCountDown2) {
        this.dismissCountDown2 = dismissCountDown2
      },
      showAlertEmail() {
        this.dismissCountDown2 = this.dismissSecs2
      }
      
    }
  }
</script>